.talentsCompo
{
    position: relative;
    height: 100%;
    min-height: 100vh;
}
.talentsCompo h1
{
    font-size: 40px;
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    text-align: center;
}
.talentsCompo h2
{
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    text-align: center;
}
.talentsCompo-all
{
    
    display: flex;
}
.talentsCompo-item
{
    width:50%;
    
}
.talentsCompo span
{
    color: #fab73c;
    font-weight: 700;
    margin-right: 5px;
}
.talentsCompo a
{
    color: #57a593;
    cursor: pointer;
    text-decoration: none ;
}
.talentsCompo a:hover,
.talentsCompo a:active
{
    color: #fab73c;
}
.talentsCompo p
{
    color:#054225;
    padding-left: 50px;
    
}
.talentsCompo img
{
    position: relative;
    top:1px;
    margin-right: 2px;
}
.talentsCompo hr
{
    background: #00FF96;
    border: none;
    height: 2px;
    width: 80%;
}
.talentsCompo-all hr
{
    background: #00FF96;
    border: none;
    height: 2px;
    margin-left: 50px;
}

@media (max-width: 768px)
{
    .talentsCompo p
    {
        padding-left: 10px;
    }
    .talentsCompo-all hr
    {
        margin-left: 10px;
    }
    .talentsCompo-item
    {
        width:100%;
    
    }
    .talentsCompo-all
{
    
    flex-direction: column;
}
}