.raidTips
{
    position: relative;
    height: 100%;
    min-height: 100vh;
    margin-left: 20%;
    margin-right: 20%;
}
.raidTipsCompo h2
{
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    font-size: 30px;
    
}
.raidTipsCompo-item
{
    display:flex;
    flex-direction: row;
}
.raidTipsCompo-item img
{
    width: 50px;
    height: 50px;
    position: relative;
    top: 15px;
    margin-right: 10px;
    border: solid 1px #00FF96;
}
.raidTipsCompo p
{
    color: #054225;
    text-align: justify;
    line-height: 25px;
    margin-top:0;
}
.hOneHr
{
    background: #00FF96;
    border: none;
    height: 2px;
}
.raidTips h1
{
    font-size: 40px;
   text-align: center;
   color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
}
@media (max-width: 768px)
{
    .raidTipsCompo-item img
    {
      width: 25px;
      height: 25px;
      top: 20px;
    }
    .raidTipsCompo-item h2
    {
        font-size: 25px;
    }
    .raidTips
    {
        margin-left: 10%;
        margin-right: 10%;
    }
 
}