.abilityCompo
{
    position: relative;
    height: 100%;
    min-height: 100vh;
}
.abilityCompo h1
{
    font-size: 40px;
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    text-align: center;
}
.abilityCompo h2
{
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    text-align: center;
}
.abilityCompo-all
{
    
    display: flex;
}
.abilityCompo-item
{
    width:50%;
    
}
.abilityCompo span
{
    color: #fab73c;
    font-weight: 700;
    margin-right: 5px;
}
.abilityCompo a
{
    color: #57a593;
    cursor: pointer;
    text-decoration: none ;
}
.abilityCompo a:hover,
.abilityCompo a:active
{
    color: #fab73c;
}
.abilityCompo p
{
    color:#054225;
    padding-left: 50px;
    
}
.abilityCompo img
{
    position: relative;
    top:1px;
    margin-right: 2px;
}
.abilityCompo hr
{
    background: #00FF96;
    border: none;
    height: 2px;
    margin-left: 50px;
}
.hOneP
{
    font-size: 24px;
    text-align: center;
    margin-right: 24px;
}
.abilityCompo-tip
{
    line-height: 30px;
}
.abilityCompo ul
{
    list-style: none;
}
@media (max-width: 768px)
{
    .abilityCompo p
    {
        padding-left: 10px;
        
        
    }
    .abilityCompo hr
    {
        margin-left: 10px;
    }
    .abilityCompo-all
    {
    
    display: flex;
    flex-direction: column;
    }
    .abilityCompo-item
    {
    width:100%;
    
    }
    .hOneP
{
    font-size: 15px;
    
}
}