
.item_fourZeroFour
{
   
    color: #00FF96;
    text-shadow: -2px 0 #054225, 0 2px #054225, 2px 0 #054225, 0 -2px #054225;
    font-size: 500%;
    
}
.fourZeroFour
{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    height: 100%;
}
.fourZeroFour-img
{
    margin: 20px;
    width: 30%;
    border: 5px solid #00FF96;
    
     

}
.fourZeroFour p
{
    color:#054225;
    font-weight: 700;
    font-size: 25px;
    
}
.fourZeroFour a
{
    color: #57a593;
    cursor: pointer;
    text-decoration: none ;
}
.fourZeroFour a:hover,
.fourZeroFour a:active
{
    color: #fab73c;
}
@media (max-width: 768px)
{
   .item_fourZeroFour
   {
       font-size: 250%;
   }
   .fourZeroFour p
   {
       text-align: center;
       margin-left: 20px;
       margin-right: 20px;
       font-size: 16px;
   }
    
}