.faqCompo
{
    position: relative;
    height: 100%;
    min-height: 100vh;
    
    
}
.faqAll
{
    display: flex;
}
.faqCompo p
{
      
    height: 30%;
    font-size: 22px;
    color: #054225;
    font-weight: 700;
    padding-bottom: 20px;
    text-align: justify;
    
    
}
.faqCompo h1
{
   
    font-size: 40px;
   text-align: center;
   color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
}
.faqCompo h2
{
    font-size: 25px;
   
    color: #00FF96;
     text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
}
.faqItem
{
    text-align: center !important;
}
.faqCompoLine
{
    
    width: 50%;
    margin: 30px;
    
}
.faqImg
{
    margin-right: 2px;
    position: relative;
    top:1px;
}
.faqCompo a
{
    color: #57a593;
    cursor: pointer;
    text-decoration: none ;
}
.faqCompo a:hover,
.faqCompo a:active
{
    color: #fab73c;
}
.faqCompo ul
{
    padding: 0;
    list-style-type: none;
}
.faqCompo img
{
    width: 18px;
    margin-right: 3px;
    position: relative;
    top: 1px;
}
@media (max-width: 768px)
{
   .faqCompo p
   {
       font-size: 12px;
       
       
   }
   .faqImg
    {
    width: 10px;
    }
    .faqCompo h2
    {
        font-size: 13px;
    }
    .faqCompoLine
    {
    
   
    margin: 10px;
    
    }
 
}