.bisCompo
{
    position: relative;
    height: 100%;
    min-height: 100vh;
}
.bisCompo h1
{
    font-size: 40px;
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    text-align: center;
}
.bisCompo h2
{
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    text-align: center;
}
.bisCompo-all
{
    
    display: flex;
}
.bisCompo-item
{
    width:50%;
    
}
.bisCompo span
{
    color: #fab73c;
    font-weight: 700;
    margin-right: 5px;
}
.neck
{
    color:#e6cc80; 
}
.back
{
    color:#ff8000; 
}
a
{
    color: #57a593;
    cursor: pointer;
    text-decoration: none ;
}
.item
{
    color:#a335ee;
}
.bisCompo a:hover,
.bisCompo a:active
{
    color: #fab73c;
}
.bisCompo p
{
    color:#054225;
    padding-left: 50px;
    
    
}
.item_p
{
    font-weight: bold;
    margin-left: 40px;
    font-size: 21px;
}
.bisCompo img
{
    position: relative;
    top:1px;
    margin-right: 2px;
}
.bisCompo hr
{
    background: #00FF96;
    border: none;
    height: 2px;
    width: 80%;
}
.bisCompo-all hr
{
    background: #00FF96;
    border: none;
    height: 2px;
    margin-left: 50px;
}
.bisCompo ul
{
    list-style: none;
}

@media (max-width: 768px)
{
    .bisCompo p
    {
        padding-left: 10px;
    }
    .bisCompo-all hr
    {
        margin-left: 10px;
    }
    .bisCompo-item
    {
        width:100%;
    
    }
    .bisCompo-all
{
    
    flex-direction: column;
}
}